html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0; }

.form {
  padding-top: 30px; }
  .form-control {
    height: 34px;
    border-radius: 0; }
  .form label {
    display: block; }
  .form .input-group {
    position: relative; }
  .form .input-addon {
    width: 40px;
    height: 34px;
    line-height: 34px;
    background: #ccc;
    color: #fff;
    display: inline-block;
    text-align: center;
    position: absolute;
    bottom: 0;
    right: 0; }
  .form .icon-row {
    text-align: right; }
    .form .icon-row span {
      border: 1px solid #ccc;
      border-radius: 2px;
      padding: 8px; }
      .form .icon-row span:hover {
        cursor: pointer; }
      .form .icon-row span:not(:last-child) {
        margin-right: 5px; }
